.user-manager-container {
  margin-top: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 1.2em;

  .editable-paragraph {
    font-family: system-ui, sans-serif;
    line-height: 1.5;
  }

  /* Each editable box + delete button */
  .token {
    display: inline-flex;
    align-items: center;
    border-bottom: 1px solid #ccc;
    /* subtle underline to look like a fill-in blank */
  }

  /* The input itself */
  .input-wrap {
    border: none;
    outline: none;
    font: inherit;
    color: white;
    padding: 0 0.2em;
    background: transparent;
    margin-right: 0.15rem;
    width: min-content;
  }

  /* Small "x" delete button */
  .delete-btn {
    border: none;
    background: transparent;
    cursor: pointer;
    padding: 0;
    margin: 0 0.2em;
    font-size: 0.8em;
    line-height: 1;
    opacity: 0.6;
    color: white;
  }

  .delete-btn:hover {
    opacity: 1;
  }
}
